@mixin min1200 {
  @media (min-width: 1200px) {
    @content;
  }
}

@mixin min992 {
  @media (min-width: 992px) {
    @content;
  }
}

@mixin min768 {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin min550 {
  @media (min-width: 550px) {
    @content;
  }
}

@mixin min375 {
  @media (min-width: 376px) {
    @content;
  }
}

@mixin boxShadow {
  box-shadow: 0 5px 15px 0 rgba(29, 51, 59, 0.2);
}

@mixin clearfix() {
  *zoom: 1;

  &::before,
  &::after {
    content: " ";
    display: table;
  }

  &::after {
    clear: both;
  }
}

